import "core-js/modules/es.array.from.js";
import "core-js/modules/es.string.iterator.js";
import "core-js/modules/es.object.to-string.js";
import "core-js/modules/es.promise.js";
import "core-js/modules/es.promise.finally.js";
import "core-js/modules/web.dom-collections.for-each.js";
// api
import InstagramMediaDTMAdapter from "Shared/ts/api/instagram/instagram-media-dtm-adapter";
import LoadItem from "Shared/ts/utils/load-item";
var instagramMedia = new InstagramMediaDTMAdapter();

var createInstagramGallery = function createInstagramGallery(gallery) {
  if (!gallery) return;
  var pictures = Array.from(gallery.querySelectorAll("[data-instagram-img-src]"));
  instagramMedia.requestImages().then(function (media) {
    pictures.forEach(function (picture, index) {
      var post = media[index];
      picture.setAttribute("data-instagram-img-src", post.media_url);
      picture.setAttribute("data-attr", JSON.stringify({
        alt: post.caption
      }));
      var link = picture.hasAttribute("href") ? picture : picture.querySelector("a[href]");
      if (!link) return;
      link.setAttribute("href", post.permalink);
    });
  }).catch(function (error) {
    console.debug(error);
  }).finally(function () {
    pictures.forEach(function (picture) {
      new LoadItem(picture, {
        tag: "img",
        src: "data-instagram-img-src"
      });
    });
  });
};

createInstagramGallery(document.querySelector(".slide--instagram"));